/* ==================================Контейнер для новости Конкретного Автора!+ первая буква+ коментарий======================== */



.comments-container {
    margin: 10px;
    
}

.div-for-icon-and-name {
    display: flex;
}

.comments-name {
    padding: 5px;
    font-weight: bold;
}

.comments-text {
    padding: 5px;
    margin-top: 10px;
}

.answer {
    text-align: right;
    padding: 5px;
    font-weight: bold;
    color: #8a8a8a;
    cursor: pointer;
}

.сomment-counter {
    color: #000000;
    font-weight: bold;
    margin-left: 10px;
}

.image-icon {
    height: 40px;
    width: 40px;
    border-radius: 50px;
    background-color: rgb(95, 117, 241);
    text-align: center;
    padding: 10px;
}
/* =================Кнопка в инпуте====================================== */
.div-for-input-and-btn {
    display: flex;
    position: relative;
}

.btn-send-in-input {
    position: absolute;
    height: 40px;
    width: 112px;
    background-color: aquamarine;
    border-radius: 10px;
    text-align: center;
    padding-top: 10px;
    font-weight: bolder;
    z-index: 999;
    right: 40px;
    bottom: 25px;
    cursor: pointer;
}

.input-add-comment {
    position: relative;
    border-radius: 10px;
    margin: 20px;
    height: 100px;
    width: 90%;
    background-color: #f0efef;
    border: 0px;
}

.icon-from-liba{
    display: flex;
    margin: 10px;
    font-size: 20px;
    
}
.icon-from-liba-div{
    margin: 10px;
}
